import React from 'react';
import Action from './action/index'
import Intro from './intro/index'
import PageNav from './page_nav/index'
import ContactInfo from './contact_info';

function Header() {
    return (
        <header class="header">
            <Action/>
            <Intro/>
            <ContactInfo/>
            <PageNav/>
        </header>
    );
}

export default Header;